@import url(https://fonts.googleapis.com/css2?family=Play&display=swap);
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #212121;
    padding-top: 50px;
    color: white;
    text-align: center;
}

.Cell {
    height: 100px;
    width: 100px;
    border-radius: 1px;
    background-color: #263238;
    transition: background-color 0.7s ease;
}

.Cell-lit {
    background-color: #6deeff;
}

@media screen and (max-width: 480px) {
    .Cell {
        height: 65px;
        width: 65px;
    }
}

.Board {
    margin: 0 auto;
    border-radius: 10px;
    /* box-shadow: 10px 10px 0 red; */
    box-shadow: 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
}

table tr:first-child td:first-child {
    border-radius: 10px 0 0 0 !important;
}
table tr:first-child td:last-child {
    border-radius: 0 10px 0 0 !important;
}
table tr:last-child td:first-child {
    border-radius: 0 0 0 10px !important;
}
table tr:last-child td:last-child {
    border-radius: 0 0 10px 0 !important;
}

@font-face {
    font-family: neon;
    src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.Board-title {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 4rem;
    font-size: 80px;
}

.neon-orange {
    font-family: neon;
    color: #fb4264;
    text-shadow: 0 0 3vw #f40a35;
    animation: neon-orange 3s ease infinite;
    -moz-animation: neon-orange 3s ease infinite;
    -webkit-animation: neon-orange 3s ease infinite;
}

.neon-blue {
    font-family: neon;
    color: #426dfb;
    text-shadow: 0 0 3vw #2356ff;
    animation: neon-blue 2s linear infinite;
    -moz-animation: neon-blue 2s linear infinite;
    -webkit-animation: neon-blue 2s linear infinite;
    -o-animation: neon-blue 2s linear infinite;
}

.winner .neon-blue,
.winner .neon-orange {
    font-size: 200px;
}

