.ui.card.main-container {
    color: black;
    background: #1cd8df;
    background: linear-gradient(to right, #fed128, #1cd8df);
    opacity: 0.9;
    box-shadow: 0 0 30px #fff, -10px 0 50px #f0f, 10px 0 50px #0ff;
    border-radius: 5px;
    padding: 5px;
}

.content {
    width: 60vw;
    font-size: 18px;
    text-align: center;
    margin-bottom: 20px;
}
.content:hover {
    cursor: pointer;
}

.button-container {
    margin: 20px 0 20px 0;
    text-align: center;
}

.header {
    text-align: center;
}
.info-container {
    text-align: center;
    font-size: 20px;
    margin-top: 10px;
    font-weight: 600;
    font-family: 'Play', sans-serif;
}

.current-song {
    margin-bottom: 10px;
}

.playlist {
    margin-left: 50px;
    font-family: 'Play', sans-serif;
}

.list {
    display: grid;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
}

.App {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100vh;
    text-align: 'center';
}

body {
    font-family: 'Open Sans';
}

.Display {
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
}

.Instruction {
    align-self: baseline;
    opacity: 0.3;
    font-size: 1rem;
}

.Instruction:hover {
    opacity: 1;
}

@media screen and (max-width: 767px) {
    .Display {
        display: block;
        margin-bottom: 2rem;
    }

    .main-container {
        margin: auto !important;
    }
    .Instruction {
        padding: 2rem;
    }
}

@media screen and (max-width: 480px) {
    body {
        padding: 0.5rem;
    }
    .BoardDisplay {
        margin-bottom: 3rem;
    }

    .Board-title {
        font-size: 4rem;
    }
}

